/* Home */
.home {
  height: 100%;
  background-color: black;
  text-align: center;
  margin: 0 auto;
  align-content: center;
  display: flex;
  flex-direction: column;
  position: relative;
  /* z-index:0; */
}

.background {
  height: 100vh;
  width: 100%;
  opacity: 0.7;
  position: relative;
  z-index: 0;
  /* background-image: url("../images/touring.jpeg"); */
  background-image: url("../images/uva.jpeg");

  background-size: 100% 100%;

  background-attachment: fixed;
  background-repeat: no-repeat;
}

.centered6 {
  position: absolute;
  text-align: center;
  margin: auto;
  padding-top: 80px;
  /* top: 60%; */
  left: 50%;
  transform: translate(-50%, -80%);
}

.me {
  margin-bottom: 40px;
  width: 230px;
  border-radius: 50%;
  z-index: 2;
  border: 4px solid white;
  box-shadow: 0 0 1px 7px rgba(255, 255, 255, 0.2);
  transition: 0.2s;
  z-index: 5;
}

.me:hover {
  box-shadow: 0 0 0px 15px rgba(255, 255, 255, 0.12);
}

.toabout {
  margin: auto;
  width: 100px;
  height: 20px;
  align-content: center;
  justify-content: center;
  text-align: center;
  padding: 10px;
  transform: translate(0%, 50%);
  border: 1px solid white;
  color: white;
  transition: 0.4s ease;
  font-weight: 300px;
  font-size: 18px;
  background-color: rgba(255, 255, 255, 0.25);
}

.toabout:hover {
  background-color: rgba(255, 255, 255, 1);
  color: black;
  /* width: 120px; */
  font-size: 15px;
  /* height: 120px; */
  cursor: grab;
  /* border-radius: 10px; */
}

.name {
  font-size: 55px;
  font-weight: 200;
  color: white;
}

.name2 {
  /* font-size: 20px; */
  font-weight: 500;
  padding-top: 10px;
  color: white;
  font-weight: 300;
}

.centered {
  position: absolute;
  text-align: center;
  margin: auto;
  top: 35%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 5;
}

.centeredb {
  position: absolute;
  text-align: center;
  margin: auto;
  top: 70%;
  left: 50%;

  transform: translate(-50%, -50%);
}

.centeredc {
  position: absolute;
  text-align: center;
  margin: auto;
  top: 85%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.icons {
  display: flex;
  margin-bottom: 500px;

  margin: 10px;
  align-content: center;
  justify-content: center;
  padding: 10px;
}

.iconpadding {
  width: 15px;
}

.icon {
  width: 15px;
  height: 15px;
  transition: 0.2s ease;

  transform: translate(0%, 30%);
}

.icon1 {
  width: 25px;
  height: 25px;
  transition: 0.2s ease;
  background-color: white;
  border: 3px solid white;
  border-radius: 50%;
  text-align: center;
  box-shadow: 0 0 1px 2px rgba(255, 255, 255, 0.25);
}

.icon1:hover {
  background-color: #aaaaaa;
  border: 3px solid #aaaaaa;
  box-shadow: 0 0 1px 4px rgba(255, 255, 255, 0.25);
}

.subhead0 {
  width: 150px;
  margin: 0 auto;
  padding-top: 10px;
  margin-bottom: 0px;
  border-bottom: 1px solid white;
  z-index: 4;
  opacity: 0.8;
}

.subhead {
  width: 100px;
  margin: 0 auto;
  padding-top: 20px;
  margin-bottom: 20px;
  border-bottom: 1px solid #eeeeee;
  opacity: 0.8;
}

.subhead2 {
  width: 25px;
  margin: 0 auto;
  padding-top: 0px;
  margin-bottom: 10px;
  border-bottom: 1px solid black;
}

.subhead3 {
  width: 100px;
  margin: 0 auto;
  padding-top: 20px;
  margin-bottom: 20px;
  border-bottom: 1px solid black;
  opacity: 0.8;
}

