/* About */
.about {
  /* height: 160vh; */
  background-color: #dddddd;
  /* z-index: 0; */
  /* position: relative; */
  /* align-content: center; */
  /* margin: 0 auto; */
  display: flex;
  flex-direction: column;
  padding-bottom: 30px;
}

.centered3 {
  display: flex;
  flex-direction: column;
  position: absolute;
  text-align: center;
  margin: auto;
  top: 18%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 0;
}

.centeredd {
  position: absolute;
  text-align: center;
  margin: auto;
  left: 50%;
  transform: translate(-50%, 0%);
  margin-top: 265px;
}

.flex {
  display: flex;
  width: 100%;
  align-content: center;
  align-items: center;
  justify-content: center;
  justify-self: center;
  align-self: center;
}

.paper2 {
  padding: 0px 20px 0px 20px;
}

.aboutpic {
  height: 450px;
  width: 350px;
  overflow: hidden;
  transition: 0.3s ease;
  padding: 0px;
  margin: 0px;
  /* border: 3px solid rgb(0,0,0,0.5); */
  box-shadow: 6px 6px 0px 6px rgb(0, 0, 0, 0.8);
  /* this is the exact culprit */
  display: block;

}

.paper {
  position: relative;
  overflow: hidden;
  margin: 0 auto;
  padding: 0;
  font-size: 20px;
  transition: 0.3s ease;
  box-shadow: 20px 20px 0px 0px rgb(0, 0, 0, 0.8);
  overflow: hidden;
  display: block;
  /* margin-left: 25px; */
}

.blackglass {
  /* REMEMBER TO SUBTRACT PAPER WIDTH - 2* PADDING for WIDTH & HEIGHT HERE! */
  /* height: 590px;
  width: 410px; */
  height: 450px;
  width: 350px;
  padding: 20px;
  width: 95%;
  overflow-y: hidden;
  background: rgba(25, 25, 25, 0.1);
  position: absolute;
  color: rgb(255, 255, 255, 0);
  font-size: 17px;
  margin: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.3s ease;
  display: block;

}

.paper:hover .blackglass {
  color: rgb(255, 255, 255, 1);
  background: rgba(25, 25, 25, 0.8);
  /* transform: scale(1.3); */
}

.paper:hover {
  box-shadow: 6px 6px 0px 0px rgb(0, 0, 0, 0.8);
  /* box-shadow: 15px 15px 0px 0px rgb(0, 0, 0, 0.8); */
}

.paper:hover .aboutpic {
  transform: scale(1.2);
  /* box-shadow: 4px 4px 0px 0px rgb(0, 0, 0, 0.8); */
}

.none4 {
  text-decoration: none;
  color: white;
  width: 0px;
  opacity: 0;
  position: relative;
}

.none4:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0%;
  border-bottom: 1px solid white;
  transition: 0.5s;
}

.blackglass:hover .none4:after {
  width: 100%;
}

.paper:hover .none4 {
  opacity: 1;
}

