@media only screen and (max-width: 700px) {
  .flex {
    display: flex;
    flex-direction: column;
    /* min-width: 100%; */
    padding-top: 0px;
    margin: auto;
    width: 100%;
  }

  .about {
    /* background-color: #eeeeee; */
    z-index: 0;
    position: relative;
    align-content: center;
    margin: 0 auto;
    display: flex;
  }

  .center {
    padding-top: 20px;
    text-align: center;
    z-index: 0;
  }

  .aboutpic {
    height: 450px;
    width: 300px;
    /* overflow: hidden; */
    transition: 0.3s ease;
    box-shadow: 6px 6px 0px 6px rgb(0, 0, 0, 0.8);
  }

  .who {
    text-align: center;
    font-weight: 300;
    font-size: 18px;
  }

  .blackglass {
    /* REMEMBER TO SUBTRACT PAPER WIDTH - 2* PADDING for WIDTH & HEIGHT HERE! */
    height: 450px;
    width: 200px;
    padding: 70px;
    width: 70%;
    background: rgba(25, 25, 25, 0);
    position: absolute;
    color: rgb(255, 255, 255, 0);
    /* font-size: 15px; */
    margin: auto;
    /* top: 50%; */
    /* left: 50%; */
    transform: translate(-50%, -50%);
    transition: 0.3s ease;
  }

  .paper {
    margin: 20px;
    box-shadow: 15px 10px 0px 0px rgb(0, 0, 0, 0.8);
  }

  .paper2 {
    /* position: relative; */
    /* overflow: hidden; */
    padding: 0px 0px 0px 0px;
  }

  .p {
    font-weight: 300;
    font-size: 11.89px;
  }
}

@media only screen and (min-width: 700px) and (max-width: 1250px) {
  .flex {
    display: flex;
    flex-direction: column;
    /* min-width: 100%; */
    padding-top: 10px;
    margin: auto;
    width: 100%;
  }

  .paper {
    margin: 20px;
    box-shadow: 15px 10px 0px 0px rgb(0, 0, 0, 0.8);
  }

  .p {
    font-weight: 300;
    font-size: 17px;
  }
}