body {
  margin: 0;
  padding: 0;
  font-family: "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.column {
  /* display: flex;
  flex-direction: column; */
}

/* NavBar */
.navz {
  z-index: 1;
}

.headpadding {
  width: 100%;
  height: 70px;
}

.centered2 {
  position: absolute;
  text-align: center;
  margin: auto;
  /* padding-top: 60px; */
  top: 50%;
  left: 50%;
  transform: translate(-50%, 0%);
}

.nav-scrolled {
  background-color: rgb(0, 0, 0, 1);
  width: 100%;
  display: flex;
  margin: 0;
  padding: 0;
  z-index: 1;
  position: fixed;
  top: 0;
  text-align: center;
  margin: 0 auto;
  align-content: center;
  align-items: center;
  align-self: center;
  justify-content: center;
  height: 70px;
}

.nav {
  background-color: black;
  width: 100%;
  display: flex;
  margin: 0;
  padding: 0;
  z-index: 1;
  opacity: 0.9;
  text-align: center;
  margin: auto;
  align-content: center;
  align-items: center;
  align-self: center;
  justify-content: center;
  height: 70px;
  transition: 0.2s;
}

.link {
  text-decoration: none;
  font-weight: 300;
  color: white;
  width: 150px;
  height: 30px;
  padding: 20px 0px 20px 0px;
  text-align: center;
  font-size: 20px;
  transition: 0.5s;
}

.link:hover {
  color: black;
  background-color: white;
  /* text-decoration: underline; */
  cursor: -webkit-grab;
  cursor: grab;
}

.active {
  color: black;
  background-color: white;
  cursor: -webkit-grab;
  cursor: grab;
}

