}

.paper {
  position: relative;
  overflow: hidden;
  margin: 0 auto;
  padding: 0;
  font-size: 20px;
  transition: 0.3s ease;
  box-shadow: 20px 20px 0px 0px rgb(0, 0, 0, 0.8);
  overflow: hidden;
  display: block;
  /* margin-left: 25px; */
}

.blackglass {
  /* REMEMBER TO SUBTRACT PAPER WIDTH - 2* PADDING for WIDTH & HEIGHT HERE! */
  /* height: 590px;
  width: 410px; */
  height: 450px;
  width: 350px;
  padding: 20px;
  width: 95%;
  overflow-y: hidden;
  background: rgba(25, 25, 25, 0.1);
  position: absolute;
  color: rgb(255, 255, 255, 0);
  font-size: 17px;
  margin: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.3s ease;
  display: block;

}

.paper:hover .blackglass {
  color: rgb(255, 255, 255, 1);
  background: rgba(25, 25, 25, 0.8);
  /* transform: scale(1.3); */
}

